@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2"), url("../fonts/opensans.woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2"), url("../fonts/opensansbold.woff");
  font-style: normal;
  font-weight: 700;
}
.item-interactivity:hover .main-nav__link[href],
.item-interactivity:focus .main-nav__link[href] {
  opacity: 0.6;
}
.item-interactivity:active .main-nav__link[href] {
  opacity: 0.3;
}
.item-interactivity:hover .main-nav__icon,
.item-interactivity:focus .main-nav__icon {
  opacity: 1;
  fill: #63d1bb;
}
.item-interactivity:active .main-nav__icon {
  opacity: 0.3;
}
img {
  max-width: 100%;
  height: auto;
}
a,
a:link,
a:visited,
a:focus,
a:hover,
a:active {
  text-decoration: none;
  color: inherit;
}
*,
*::before,
*::after {
  box-sizing: border-box;
  /* outline: 0; */
}
textarea {
  resize: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.contacts {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}
@media (min-width: 768px) {
  .contacts {
    flex-direction: row;
  }
}
.contacts__item {
  font-size: 15px;
  font-weight: 400;
  line-height: 30px;
  margin-bottom: 29px;
}
@media (min-width: 768px) {
  .contacts__item {
    font-size: 17px;
    min-width: 334px;
    margin: 0;
  }
}
@media (min-width: 1150px) {
  .contacts__item {
    padding-bottom: 29px;
  }
}
.contacts__item address {
  font-style: normal;
}
.contacts__type {
