}
.categories-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .categories-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: end;
  }
}
.categories-list__item {
  background-color: #63d1bb;
  text-align: left;
  padding-left: 30px;
  padding-top: 26px;
  padding-right: 27px;
  border: 0;
  width: 100%;
  min-height: 103px;
  display: flex;
  justify-content: space-between;
  align-items: start;
}
@media (min-width: 768px) {
